.notes-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  margin-top: 15px;

  min-width: 300px;
  max-width: 600px;
  width: 100%;
}

.notes-header {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;

  font-size: 22px;

  gap: 15px;
}


.notes-btn {
  height: 35px;
  width: 35px;
  
  border: 1px solid grey;
  border-radius: 50%;

  cursor: pointer;

  user-select: none;
  transition: transform 0.2s ease;
}

.notes-btn:hover {
  box-shadow: 0 0 3px 0 grey;
}

.notes-btn:active {
  transform: scale(0.9) rotate(360deg);
}

